import React, { PureComponent } from "react"; 
import { convertToAtob, kFormatter } from "../../../../utils/Common";
import { GET_NETWORK_NAME } from "../../../../utils/Network";
import "./scss/SocialAction.scss";

const NetworkAction = (networkID, postID, Link, action) => {
  const actions = {
    like: `https://twitter.com/intent/favorite?tweet_id=${convertToAtob(postID)}`,
    comment: `https://twitter.com/intent/tweet?in_reply_to=${convertToAtob(postID)}`,
    retweet: `https://twitter.com/intent/retweet?tweet_id=${convertToAtob(postID)}`
  };
  return networkID === 1 ? actions[action] : Link;
};

const SocialItm = ({ label, hrefClick, Icon, count, itemId = 0, type }) => {
  const countNo = count > 0 ? kFormatter(count) : "";
  return <div className="tb_social_action__list" role="listitem" aria-roledescription="post social action links">
    <div onClick={event => { event.stopPropagation(); window.open(hrefClick, "_blank"); }} className={`tb_social_action__ico_wrap tb_social_action__ico_wrap_${itemId}`} aria-label={`${label} post ${type} count ${countNo}`} role="button" tabIndex="0">
      <div className={`tb_social_action_ico__ tb__icon tb-${Icon}`}>  </div>
      {countNo ? <div className="tb_social_action_counts__">{countNo}</div>:''}
    </div>
  </div>
};

export default class SocialAction extends PureComponent {
  componentDidMount() {
    try {
      const { Post } = this.props;
      const socialActionId = `.tb_social_action__ico_wrap_${Post?.id}`;
      document.querySelector(socialActionId)?.addEventListener('keydown', function (event) {
        if (event.key === 'Enter' || event.key === ' ') {
          event.preventDefault();
          this.click(); // Triggers the existing onClick functionality
        }
      });
    } catch (e) {
      console.log(e);
    }
  }

  render() {
    const { Post, CardStyle, actionClass } = this.props;
    const { icon, style } = CardStyle;
    const iconStyle = { color: icon.default==1 ? icon.color : style.color };

    const networkID = Post.networkId;
    const { comment, like } = Post.count;
    const likeIcon = [3, 10, 7].includes(networkID) ? 'like' : 'heart-outline';
    const likeIconStatus = [1, 2, 3, 7, 8, 9, 10, 18].includes(networkID);
    const commentIconStatus = ![29, 19].includes(networkID);
    const networkName = GET_NETWORK_NAME(networkID);

    return ![29, 26, 23, 6, 5, 11, 12, 15, 20, 21].includes(networkID) ? (
      <div className={actionClass}>
        <div className="tb_social_action__" role="list">
          {likeIconStatus && <SocialItm itemId={Post.id} label={networkName} hrefClick={NetworkAction(networkID, Post.postId, Post.link, 'like')} Icon={likeIcon} iconStyle={iconStyle} count={like} type="like" />}
          {commentIconStatus && <SocialItm itemId={Post.id} label={networkName} hrefClick={NetworkAction(networkID, Post.postId, Post.link, 'comment')} Icon={"comment"} iconStyle={iconStyle} count={comment} type="comment" />}
          {networkID === 1 && <SocialItm itemId={Post.id} label={networkName} hrefClick={NetworkAction(networkID, Post.postId, Post.link, 'retweet')} Icon={"retweet"} iconStyle={iconStyle} count={comment} type="comment" />}
          <SocialItm label={networkName} itemId={Post.id} hrefClick={Post.link} Icon={"eye"} iconStyle={iconStyle} count={0} type="view" />
        </div>
      </div>
    ) : null;
  }
}